import Vue from 'vue'
import config from '@/config'

const STATS = 'STATS'
const STATS_SUCCESS = 'STATS_SUCCESS'
const STATS_FAILED = 'STATS_FAILED'

const USERS = 'USERS'
const USERS_SUCCESS = 'USERS_SUCCESS'
const USERS_FAILED = 'USERS_FAILED'

const moduleStats = {
  state: {
    users: {},
    stats: {}
  },
  mutations: {
    [STATS] (state) {
      state.statsPending = true
    },
    [STATS_SUCCESS] (state, stats) {
      state.statsPending = false
      state.stats[stats.date] = stats
    },
    [STATS_FAILED] (state) {
      state.statsPending = false
    },
    [USERS] (state) {
      state.usersPending = true
    },
    [USERS_SUCCESS] (state, user) {
      state.users[user.id] = user
      state.usersPending = false
    },
    [USERS_FAILED] (state) {
      state.usersPending = false
    }
  },
  actions: {
    getStats ({ commit }, date) {
      commit(STATS)
      let url = config.API_URL + '/scores'
      if (date) {
        url += '?date=' + date
      }
      return Vue.http.get(url)
        .then(response => response.json())
        .then(json => {
          commit(STATS_SUCCESS, json)
          return json
        }).catch(() => {
          commit(STATS_FAILED)
        })
    }
  },
  getters: {
    getUsers: state => {
      return state.users
    },
    getStats: state => {
      return state.stats
    }
  }
}

export default moduleStats



