<template>
  <div class="card">
    <h2 class="card-header">
      {{title}}
    </h2>
    <div>
      <UserScore
        v-for="user in users"
        v-bind:key="user.id"
        v-bind:datetime="datetime"
        v-bind:user="user">
      </UserScore>
    </div>
  </div>
</template>

<script>
import UserScore from './UserScore'

export default {
  name: 'score-card',
  components: {
    UserScore
  },
  data () {
    return {
      users: []
    }
  },
  props: {
    title: String,
    datetime: String
  },
  methods: {
    refresh () {
      this.$store.dispatch('getStats', this.datetime)
        .then((json) => {
          this.users = json
        })
      let $this = this
      setTimeout(function () {
        $this.refresh()
      }, 1000)
    }
  },
  mounted () {
    this.refresh()
  }
}
</script>

<style>
  .card {
    max-width: 400px;
    margin: 0 auto;
  }

  .card h2 {
    padding: 30px;
    background-color: rgba(255,255,255,0.2);
    border-radius: 3px;
  }

  .card .card-user {
    position: relative;
    text-align: left;
    padding: 20px 30px 20px 40px;
    background-color: rgba(255,255,255,0.95);
    border-radius: 3px;
    margin: 15px 0;
    box-shadow: rgba(0, 0, 0, 0.117647) 0px 1px 6px, rgba(0, 0, 0, 0.117647) 0px 1px 4px;
  }

  .card-user_name {
    color: #c85976;
  }

  .card-user_score {
    float: right;
  }

  .card-user_score i.material-icons {
    vertical-align: text-bottom;
    font-size: 18px;
    display: none;
    position: absolute;
    top: 20px;
    left: 15px;
  }

  .card-user:nth-child(1) .card-user_score i.material-icons,
  .card-user:nth-child(2) .card-user_score i.material-icons,
  .card-user:nth-child(3) .card-user_score i.material-icons {
    display: inline-block;
  }

  .card-user:nth-child(1) i.material-icons,
  .card-user:nth-child(1) .card-user_name {
    color: #FB8C00;
  }

  .card-user:nth-child(2) i.material-icons,
  .card-user:nth-child(2) .card-user_name {
    color: #78909C;
  }

  .card-user:nth-child(3) i.material-icons,
  .card-user:nth-child(3) .card-user_name {
    color: #8D6E63;
  }


  .card-user_name {
    font-weight: 600;
  }
</style>



