<template>
  <div class="card-user">
    <span class="card-user_name">
      {{user.name}}
    </span>
    <span class="card-user_score">
      <span v-if="datetime">{{user.scores[datetime]}}</span>
      <span v-if="!datetime">{{user.total_score}}</span>
      <span></span>
      <i class="material-icons">&#xE885;</i>
    </span>
  </div>
</template>

<script>
export default {
  name: 'score-user',
  props: {
    user: Object,
    datetime: String,
    score: Number
  }
}
</script>

<style>
.card-user_name {
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}
</style>



